import ProductCard from './ProductCard'
import {Component} from 'react'
import PropTypes from 'prop-types'

class ProductPane extends Component {

    render() {

        const {store} = this.context
        const {products} = store.getState()
        console.log(products)

        return (

            <div className="ui stackable five column grid centered">
                {products.forEach((item, index) => <div className="column" key={item}><ProductCard/></div>)}
            </div>
        )
    }
}

ProductPane.contextTypes = {
    store: PropTypes.object
}

export default ProductPane


let a = `<div className="ui stackable five column grid centered">
    {products.forEach((item, index) => <div className="column"><ProductCard key={index} info={item}/></div>)}
</div>`



